import Vue from 'vue'
import Vuex from 'vuex'
import {request} from '@/common/js/request'

Vue.use(Vuex)

const store = new Vuex.Store({
	state: {
		openExamine: false,//
		token: '',
		userInfo: {},
		timerIdent: false,//1s
		cartCount: 0, //
		orderCount: {}, //
		couponCount: 0, //
	},
	getters: {
		hasLogin(state){
			return !!state.token;
		}
	},
	mutations: {
		//state
		setStateAttr(state, param){
			if(param instanceof Array){
				for(let item of param){
					state[item.key] = item.val;
				}
			}else{
				state[param.key] = param.val;
			}
		},
		//token
		setToken(state, data){
			const {token, tokenExpired} = data;
			state.token = token;
			uni.setStorageSync('uniIdToken', token);
			uni.setStorageSync('tokenExpired', tokenExpired);
			this.dispatch('getUserInfo'); //
			this.dispatch('getCartCount');//
			uni.$emit('refreshCart');//
			this.dispatch('getOrderCount'); //
		},
		//
		logout(state){
			state.token = '';
			uni.removeStorageSync('uniIdToken');
			this.dispatch('getCartCount');//
			uni.$emit('refreshCart');//
			this.dispatch('getOrderCount'); //
			setTimeout(()=>{
				state.userInfo = {};
			}, 1100)
		},
	},
	actions: {
		//
		async getUserInfo({state, commit}){
			const res = await request('user', 'get', {}, {
				checkAuthInvalid: false
			});
			if(res.status === 1){
				const userInfo = res.data;
				commit('setStateAttr', {
					key: 'userInfo',
					val: userInfo
				})
			}
		},
		//
		async getCartCount({state, commit}){
			let count = 0;
			if(state.token){
				try {
					const res = await request('cart', 'count');
					count = res.total || 0;
				}catch (err){
					console.error(' => ', err);
				}
			}
			commit('setStateAttr', {
				key: 'cartCount',
				val: count
			})
		},
		//
		async getOrderCount({state, commit}){
			let data = {
				c0: 0,
				c1: 0,
				c2: 0,
				c3: 0
			}
			if(state.token){
				try {
					const res = await request('order', 'getOrderCount');
					data = res;
				}catch (err){
					console.error(' => ', err);
				}
			}
			commit('setStateAttr', {
				key: 'orderCount',
				val: data
			})
		},
		//
		async getCouponCount({state, commit}){
			let count = 0;
			if(state.token){
				try {
					const res = await request('coupon', 'getUserCouponCount');
					count = res.total;
					console.log(res);
				}catch (err){
					console.error(' => ', err);
				}
			}
			commit('setStateAttr', {
				key: 'couponCount',
				val: count
			})
		},
	}
}) 


export default store
