import {request} from '@/common/js/request'

export default{
	data(){
		return {
			page: 0, //
			pageNum: 6, //
			loadingType: 1, //0 1 2
			isLoading: false, //
			loaded: false, //
		}
	},
	methods: {
		log(data){
			console.log(JSON.parse(JSON.stringify(data)))
		},
		
		
		/**
		 * navigatorTo
		 * @param {String} url
		 * @param {Object} options
		 * @param {Boolean} options.login   
		 */
		navTo(url, options={}){
			this.$util.throttle(()=>{
				if(!url){
					return;
				}
				if((~url.indexOf('login=1') || options.login) && !this.$store.getters.hasLogin){
					url = '/pages/auth/login';
				}
				uni.navigateTo({
					url
				})
			}, 300)
		},
		/**
		 * $request
		 * @param {String} module
		 * @param {String} operation
		 * @param {Boolean} data 
		 * @param {Boolean} ext 
		 * @param {Boolean} ext.showLoading loading
		 * @param {Boolean} ext.hideLoading loading
		 * @param {Boolean} ext.login 
		 * @param {Boolean} ext.setLoaded 
		 */
		$request(module, operation, data={}, ext={}){
			if(ext.login && !this.$util.isLogin()){
				return;
			}
			if(ext.showLoading){
				this.isLoading = true;
			}
			return new Promise((resolve, reject)=> {
				request(module, operation, data, ext).then(result => {
					if(ext.hideLoading !== false){
						this.isLoading = false;
					}
					setTimeout(()=>{
						if(this.setLoaded !== false){
							this.loaded = true;
						}
					}, 100)
					this.$refs.confirmBtn && this.$refs.confirmBtn.stop();
					resolve(result);
				}).catch((err) => {
					reject(err);
				})
			})
		},
		imageOnLoad(data, key){
			setTimeout(()=>{
				this.$set(data, 'loaded', true);
			}, 100)
		},
		showPopup(key){
			this.$util.throttle(()=>{
				this.$refs[key].open();
			}, 200)
		},
		hidePopup(key){
			this.$refs[key].close();
		},
		stopPrevent(){},
	},
}